    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 50px
}

.cart-header {
    font-weight: bold;
    font-size: 1.25em;
    color: #333;
    
}

.cart-column {
    display: flex;
    align-items: center;
    border-bottom:1px solid black;
    margin-right: 2em;
    margin-left: 2em;
    padding-bottom: 10px;
    margin-top: 40px;


}

.cart-row {
    display: flex;

    
}

.cart-item {
    width: 150%;
}

.cart-points {
    width: 100%;
    font-size: 1.2em;
    color: #333;
}

.cart-quantity {
    width: 20%;
}

.cart-item-title {
    color: #333;
    margin-left: .5em;
    font-size: 1.2em;
}

.cart-item-image {
    width: 75px;
    height: auto;
    border-radius: 10px;
}

.btn-danger {
    color: white;
    background-color: #EB5757;
    border: none;
    border-radius: .3em;
    font-weight: bold;
}

.btn-danger:hover {
    background-color: #CC4C4C;
}

.cart-quantity-input {
    height: 34px;
    width: 50px;
    border-radius: 5px;
    border: 1px solid #56CCF2;
    background-color: #eee;
    color: #333;
    padding: 0;
    text-align: center;
    font-size: 1.2em;
    margin-right: 25px;
}


.cart-total {
    text-align: end;
    margin-top: 40px;
    margin-right: 10px;
}

.cart-total-title {
    font-weight: bold;
    font-size: 1.5em;
    color: black;
    margin-right: 20px;

}

.cart-total-points {
    color: #333;
    font-size: 1.5em;
}

.btn-recycle {
