* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background: #333;
  color: #ffffff;
  line-height: 1.6;
  height: 100vh; /* Matters for scroll behaviour */
  overflow: hidden; /* Hides scroll bar */
}

.home_content {
  width: 100%;
  height: 100vh; /* For some reason, use 100vh here instead of 100% */

  /* CSS Smooth Scroll */
  overflow-y: scroll;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
}

.navbar {
  position: fixed;
  top: 0;
  z-index: 1;
  display: flex;
  width: 100%;
  height: 60px;
  background: rgba(0, 0, 0, 0.7);
}

.navbar ul {
  display: flex;
  list-style: none;
  width: 100%;
  justify-content: center;
}

.navbar ul li {
  margin: 0 1rem;
  padding: 1rem;
}

.navbar ul li a {
  text-decoration: none;
  text-transform: uppercase;
  color: #f4f4f4;
}

.navbar ul li a:hover {
  color: skyblue;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100vh;
  scroll-snap-align: center;
}

section h1 {
  font-size: 4rem;
}

section p {
  font-size: 1.5rem;
}

/* Section Images */
section#home {
  background: url("https://i.ytimg.com/vi/XBPjVzSoepo/maxresdefault.jpg")
    no-repeat center center/ cover;
}

section#plastic {
  background: url("https://superfoodsrx.com/wp-content/uploads/2016/09/reusing-plastic-bottles.jpg")
    no-repeat center center/ cover;
}

section#ewaste {
  background: url("https://cms.qz.com/wp-content/uploads/2017/01/electronicwaste.jpeg?quality=75&strip=all&w=1600&h=900&crop=1")
    no-repeat center center/ cover;
}

